import {GridColDef, GridColumns, GridRenderCellParams} from "@mui/x-data-grid";
import {renderRating} from "@mui/x-data-grid-generator";
import Tooltip from '@mui/material/Tooltip';
import Box from '@mui/material/Box';
import InfoIcon from '@mui/icons-material/Info';
import {Avatar, IconButton} from "@mui/material";
import {Status} from "../ui/status/status.component";
import * as React from "react";
import {useState} from "react";
import {IAnime, IncotermProps, UserAnimeProps} from "../types/types";
import {TouchRippleActions} from "@mui/material/ButtonBase/TouchRipple";
import RemoveIcon from '@mui/icons-material/Remove';
import AddIcon from '@mui/icons-material/Add';
import animeServices from "../sevices/anime.services";

export function getColumnNames(object: Object) {
    return Object.getOwnPropertyNames(object)
}

const Incoterm = React.memo(function Incoterm(props: IncotermProps) {
    const {value} = props;

    if (!value) {
        return null;
    }

    const valueStr = value.toString();
    const tooltip = valueStr.slice(valueStr.indexOf('(') + 2, valueStr.indexOf(')'));
    const code = valueStr.slice(1, valueStr.indexOf('(')).trim();

    return (
        <Box sx={{display: 'flex', alignItems: 'center', justifyContent: 'space-between'}}>
            <span>{code}</span>
            <Tooltip title={tooltip}>
                <InfoIcon sx={{color: '#2196f3', alignSelf: 'center', ml: '8px'}}/>
            </Tooltip>
        </Box>
    );
});

export function renderIncoterm(params: GridRenderCellParams<any, string | any, any>) {
    return <Incoterm value={params.value}/>;
}

export function renderAvatar(params: GridRenderCellParams<any, { name: string }, any>) {
    switch (params.value) {
        case null: {
            return ''
            break;
        }
        default: {
            return (<Avatar alt={params.value} src={params.value}/>)
            break;
        }
    }
}


function customRenderStatus(params: GridRenderCellParams<string>) {

    if (params.value == null) {
        return '';
    }

    return <Status status={params.value}/>;
}

const RenderSubscribe = (props: GridRenderCellParams<boolean>) => {
    const {value, row} = props;
    const buttonElement = React.useRef<HTMLButtonElement | null>(null);
    const rippleRef = React.useRef<TouchRippleActions | null>(null);
    const [sub, setSub] = useState<boolean>(false)

    function subscribe(event: React.MouseEvent<HTMLButtonElement>) {
        event.preventDefault()
        let data = new FormData()
        data.append('anime_id', row.anime_id)
        animeServices.subscribe(data).subscribe((response) => {
            setSub(true)
        })
    }

    function unsubscribe(event: React.MouseEvent<HTMLButtonElement>) {
        event.preventDefault()
        animeServices.unsubscribe(row.anime_id).subscribe((response) => {
            setSub(false)
        })
    }

    React.useLayoutEffect(() => {
        if (value) {
            const input = buttonElement.current?.querySelector('input');
            setSub(value)
            input?.focus();
        } else if (rippleRef.current) {
            // Only available in @mui/material v5.4.1 or later
            rippleRef.current.stop({} as any);
            setSub(false)
        }
    }, [value]);

    return (
        <strong defaultValue={row.anime_id}>
            {value}
            {sub ? <IconButton ref={buttonElement} touchRippleRef={rippleRef} defaultValue={row.anime_id}
                                 onClick={unsubscribe}
            color={"secondary"}><RemoveIcon/></IconButton> : <IconButton ref={buttonElement}
                                                                        touchRippleRef={rippleRef}
                                                                        onClick={subscribe}
                                                                        defaultValue={row.anime_id}
            color={'secondary'}>
                <AddIcon/>
            </IconButton>}
        </strong>
    )
}

export function generateColumns(object: IAnime, row: IAnime[]): GridColumns {
    let grid: GridColDef;
    let arr: GridColumns = [];
    let options: string[] = [];
    getColumnNames(object).map((value) => {
        switch (value) {
            case 'rating': {
                grid = {
                    field: value,
                    headerName: value.charAt(0).toUpperCase() + value.slice(1),
                    width: 162,
                    renderCell: renderRating,
                    type: 'number'
                }
                arr.push(grid)
                // return grid
                break;
            }
            case 'image': {
                grid = {
                    field: value,
                    width: 100,
                    headerName: value.charAt(0).toUpperCase() + value.slice(1),
                    renderCell: renderAvatar
                }
                arr.push(grid)
                break;
                // return grid;
            }
            case 'quality': {
                grid = {
                    field: value,
                    width: 100,
                    headerName: value.charAt(0).toUpperCase() + value.slice(1),
                    renderCell: customRenderStatus,
                    type: 'number'
                }
                arr.push(grid)
                // return grid
                break;
            }
            case 'full_name': {
                grid = {
                    field: value,
                    headerName: 'Name',
                    renderCell: renderIncoterm,
                    valueOptions: options,
                    width: 140,
                    type: 'singleSelect'
                }
                arr.push(grid)
                break;
            }
            case 'subscribe': {
                grid = {
                    field: value,
                    width: 100,
                    headerName: value.charAt(0).toUpperCase() + value.slice(1),
                    type: 'boolean',
                    renderCell: RenderSubscribe
                }
                arr.push(grid)
                // return grid
                break;
            }
            case 'name': {
                // return grid/
                break
            }
            case 'anime_id': {
                // return grid/
                break
            }
            default: {
                grid = {
                    field: value,
                    headerName: value.charAt(0).toUpperCase() + value.slice(1),
                    width: 100
                }
                arr.push(grid)
                // return grid
            }
        }
    })
    return arr
}

export function rowCustomisation(rows: IAnime[], userProps?: UserAnimeProps[]) {
    let ar: any[] = []
    let subscribe: boolean = false;
    rows.map((row, index) => {
        switch (true) {
            case (row.quality <= 360): {
                ar.push({
                    id: index,
                    _id: index,
                    image: row.image,
                    full_name: row.full_name,
                    country: row.country,
                    episodes: row.episodes,
                    quality: 'Low',
                    rating: row.rating,
                    release_date: row.release_date,
                    time: row.time,
                    actions: '',
                    anime_id: row.id,
                    subscribe: row.subscribe
                })
                break;
            }
            case (row.quality <= 480): {
                ar.push({
                    id: index,
                    _id: index,
                    image: row.image,
                    full_name: row.full_name,
                    country: row.country,
                    episodes: row.episodes,
                    quality: 'Good',
                    rating: row.rating,
                    release_date: row.release_date,
                    time: row.time,
                    anime_id: row.id,
                    subscribe: row.subscribe
                })
                break;
            }
            case (row.quality <= 720): {
                ar.push({
                    id: index,
                    _id: index,
                    image: row.image,
                    full_name: row.full_name,
                    country: row.country,
                    episodes: row.episodes,
                    quality: 'HD',
                    rating: row.rating,
                    release_date: row.release_date,
                    time: row.time,
                    anime_id: row.id,
                    subscribe: row.subscribe
                })
                break;
            }
            case (row.quality <= 1080): {
                ar.push({
                    id: index,
                    _id: index,
                    image: row.image,
                    full_name: row.full_name,
                    country: row.country,
                    episodes: row.episodes,
                    quality: 'HDR',
                    rating: row.rating,
                    release_date: row.release_date,
                    time: row.time,
                    anime_id: row.id,
                    subscribe: row.subscribe
                })
                break;
            }
        }

    })
    return ar
}