import * as React from "react";
import './Menu.css'
import AnimeServices from "../../sevices/anime.services";
import {GridColDef} from "@mui/x-data-grid";
import VerticalTabs from "../../ui/tab-panel/vertical-tabs/vertical-tabs.component";
import FormatShapesSharpIcon from '@mui/icons-material/FormatShapesSharp';
import sessionService from "../../sevices/session.service";
import EqualizerIcon from '@mui/icons-material/Equalizer';
import {generateColumns, rowCustomisation} from "../../helpers/toolls";
import {IAnime, ObjectId, TabComponent, TabProps, UAnime, UserAnimeProps} from "../../types/types";
import TableComponent from "../../ui/table/table.component";

export interface MenuStates {
    rows: UAnime[];
    fields: GridColDef[];
    result: TabResult;
    UserProps: UserAnimeProps[];
}

export interface TabResult {
    components: TabComponent[],
    props: TabProps[];
}

export default class MenuBodyComponent extends React.Component<any, MenuStates> {

    id: ObjectId = {
        "$oid": ''
    }
    private rows: IAnime[] = [];

    constructor(props: any) {
        super(props);
        this.putAnime = this.putAnime.bind(this)
        this.getAnime = this.getAnime.bind(this)
        this.state = {rows: [], fields: [], result: {components: [], props: []}, UserProps: []}
        this.generateTabs = this.generateTabs.bind(this)
        this.a11yProps = this.a11yProps.bind(this)
    }

    // componentDidUpdate(prevProps: Readonly<any>, prevState: Readonly<MenuStates>, snapshot?: any) {
    //     if (prevState.rows !== this.state.rows){
    //         this.putAnime()
    //     }
    // }

    componentDidMount() {

        this.putAnime()
    }

    putAnime() {
        let rows: IAnime[] = [];
        AnimeServices.getAll().subscribe((response) => {
            response.data.data.map((anime, index) => {
                rows.push({
                    id: anime.id,
                    image: anime.image,
                    full_name: anime.full_name,
                    quality: anime.quality,
                    episodes: anime.episodes,
                    release_date: anime.release_date,
                    rating: anime.rating,
                    time: anime.time,
                    subscribe: anime.subscribe,
                    country: anime.country,
                    name: anime.name
                })
            })
        }, ((e) => alert(e)), (() => this.generateTabs(rows)))

    }

    getAnime(): IAnime[] {
        let result: IAnime[] = []
        AnimeServices.getAll().subscribe((response) => {
            result = response.data.data
        })
        return result
    }


    generateTabs(rows: IAnime[]) {
        console.log(rows)
        let tabs: TabComponent[] = []
        let props: TabProps[] = []
        if (sessionService.isAdmin()) {
            tabs.push(
                {
                    component: <TableComponent rows={rowCustomisation(rows)}
                                               columns={(row: IAnime[]) => generateColumns(rows[1], rows)}
                                               key={0}/>,
                },
                {
                    component: <TableComponent rows={rowCustomisation(rows)}
                                               columns={(row: IAnime[]) => generateColumns(rows[1], rows)}
                                               key={1}/>,
                }
            )
            props.push(
                {
                    label: 'Anime List',
                    icon: <FormatShapesSharpIcon/>
                },
                {
                    label: 'Manage Anime',
                    icon: <EqualizerIcon/>
                })
        } else {
            tabs.push(
                {
                    component: <TableComponent rows={rowCustomisation(rows)}
                                               columns={(row: IAnime[]) => generateColumns(rows[1], rows)}
                                               key={0}/>,
                }
            )
            props.push(
                {
                    label: 'Anime List',
                    icon: <FormatShapesSharpIcon/>
                })
        }
        return this.setState({
            result: {
                components: tabs,
                props: props
            }
        })
    }

    a11yProps(index: number) {
        return {
            id: `vertical-tab-${index}`,
            'aria-controls': `vertical-tabpanel-${index}`,
        };
    }

    render() {
        return (
            <div>
                <VerticalTabs other={this.state.result.props} tabComponent={this.state.result.components} key={1}/>
            </div>
        );
    }
}